form {
    width: 90%;
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #1E1E1F;
    margin-bottom: 26px;
}

textarea {
    background-color: #F9FBFC;
    border: 2px solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
}

textarea:focus {
    outline-color: #003476;
}

.TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 40px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
}

.TodoForm-button--add{
    background-color: #003476;
    box-shadow: 0px 5px 25px rgba(97, 153, 250, 0.5);
    color: white;
}

.TodoForm-button--cancel{
    background-color: rgb(204, 9, 9);
    box-shadow: 0px 5px 25px rgba(250, 97, 110, 0.5);
    color: white;
}